  position: absolute;
  top: -8px;
  right: -8px;
  background-color: #efefef;
  color: #000;
  border-radius: 50%;
  width: 23px;
  height: 16px;
  font-size: 8px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.blocked-results {
  display: flex;
  gap: 8px;
  align-items: center;
  position: relative;

  svg {
    fill: rgb(0, 0, 0);
    transition: fill 0.3s ease;
  }
}

.blocked-svg-container {
  padding-right: 1.2rem;
  padding-left: 1.2rem;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #fff;
  height: 45%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
